import * as React from 'react';
import SearchBar from './SearchBar';

/**
 * アプリ本体となる「FilterableWeatherコンポーネントのprops」
 */
interface FilterableWeatherTableProps {
}

/**
 * アプリ本体となる「FilterableWeatherコンポーネントのstate」
 */
interface FilterableWeatherTableState {
  filterText: string;
}

/**
 * アプリ本体となる「FilterableWeatherコンポーネント」
 */
class FilterableWeatherTable extends React.Component<FilterableWeatherTableProps, FilterableWeatherTableState> {
  constructor(props: FilterableWeatherTableProps) {
    super(props);
    /// stateの初期化
    this.state = {
      filterText: '',
    }
    /// 対象メソッドにthisをバインディング
    this.handleFilterTextChange = this.handleFilterTextChange.bind(this);
  }

  /**
   * inputの入力をstateにセットするメソッド
   * @param e 
   */
  handleFilterTextChange(e: any): void {
    this.setState({
      filterText: e.target.value,
    });
  }

  /**
   * レンダリング
   */
  render() {
    return (
      <div>
        <SearchBar value={this.state.filterText} onFilterChange={this.handleFilterTextChange} />
      </div>
    );
  }
}

export default FilterableWeatherTable;


